




















































































































































































































































































@import 'settings';

.c-custom-layer-map {
  position: relative;
}

.c-custom-layer-map__map,
.c-custom-layer-map__map .map-view {
  width: 100%;
  min-height: 300px;

  @media (min-height: 500px) {
    min-height: 400px;
  }

  @media (min-height: 800px) {
    min-height: 500px;
  }

  @media (min-height: 900px) {
    min-height: 700px;
  }
}

.c-custom-layer-map-infowindow {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 40vh;

  @include breakpoint(large) {
    width: $global-width;
    height: 100%;
    left: 50%;
    margin-left: -0.5 * $global-width;
  }
}

.c-custom-layer-map-infowindow__container {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  height: 100%;
  width: 100%;
  background: rgba(255, 255, 255, 0.8);

  @include breakpoint(large) {
    height: 70%;
    width: 20rem;
  }
}

.c-custom-layer-map-infowindow__body {
  position: relative;
  padding: 1.5rem;
  height: 100%;

  @include breakpoint(small down) {
    img {
      max-height: 120px;
    }
  }
}

.c-custom-layer-map-infowindow__content {
  height: 100%;
  overflow-y: scroll;
}

.c-custom-layer-map-infowindow__close {
  position: absolute;
  top: .5rem;
  right: .5rem;
  z-index: 3;
}
