

















































































































@import 'settings';

.c-agenda__event {
  display: flex;
}

.c-agenda__event-date {
  display: block;
}

.c-agenda__event-time {
  min-width: 60px;
}

.c-agenda__event-title {
  flex: 1;

  .c-agenda__event-title-link  {
    white-space: normal;
  }
}

.c-agenda__eventgroup {
  margin-bottom: 1rem;
}

.c-agenda__more {
  display: inline-block;
  margin-top: 1rem;
}
