@import "~leaflet.markercluster/dist/MarkerCluster.css";
@import "settings";

.marker-cluster {
  border-radius: 50%;
  background-clip: padding-box;
}

.marker-cluster div {
  color: #fff;
  font-family: #{$body-font-family};
  font-weight: bold;
  text-align: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-left: 5px;
  margin-top: 5px;
}

.marker-cluster span {
  line-height: 30px;
}

.marker-cluster-small,
.marker-cluster-medium {
  background-color: transparentize(#000, .6);
}

.marker-cluster-small div {
  background-color: transparentize(#000, .5);
}

.marker-cluster-medium div {
  background-color: transparentize(#000, .1);
}

.marker-cluster-small:hover,
.marker-cluster-medium:hover {
  background-color: transparentize(#000, .5);

  div {
    background-color: transparentize(#000, 0);
  }
}

.c-intention-map-layer {
  position: relative;
}

.c-intention-map-layer,
.c-intention-map-layer__map {
  width: 100%;
  min-height: 300px;
  z-index: 1;

  @media (min-height: 500px) {
    min-height: 400px;
  }

  @media (min-height: 800px) {
    min-height: 600px;
  }

  @media (min-height: 900px) {
    min-height: 750px;
  }
}

.c-intention-map-layer__legend,
.c-intention-map-layer__iteminfo,
.c-intention-map-layer__cta {
  position: absolute;
  z-index: 2;
  background: rgba(255, 255, 255, 0.9);
}

.c-intention-map-layer__legend {
  top: 0;
  right: 2rem;
  padding: 1rem;
  width: 20em;
  max-height: calc(100% - 8rem);
  overflow-y: auto;

  @include breakpoint(medium down) {
    right: 0;
    max-width: 40%;
    max-height: 50%;
  }
}

.c-intention-map-layer__iteminfo {
  bottom: 0;
  left: 2rem;
  width: 33%;
  height: 70%;

  @media (min-height: 800px) {
    max-height: 80%;
    height: auto;
  }

  @include breakpoint(medium down) {
    top: auto;
    left: 0;
    right: 0;
    width: 100%;
    height: 40%;
  }
}

.c-intention-map-layer__cta {
  bottom: 2rem;
  right: 2rem;
  padding: 1rem;
  overflow-y: auto;

  @include breakpoint(medium down) {
    display: none;
  }
}

.legend-item__header {
  display: flex;
  align-items: center;
}

.legend-item__color {
  width: 1rem;
  height: 1rem;

  @include breakpoint(medium down) {
    width: .8rem;
    height: .8rem;
  }
}

.legend-item__caption {
  padding-left: .5rem;
  flex: 1;

  @include breakpoint(medium down) {
    font-size: 80%;
  }
}

.legend-item--expanded {
  &:not(:first-of-type) {
    margin-top: 1rem;
  }

  &:not(:last-of-type) {
    margin-bottom: 1rem;
  }

  .legend-item__caption {
    font-weight: bold;
  }
}

.legend-item__items {
  list-style-type: none;
  margin-left: 0;
  margin-bottom: 0;
}

.legend-item__items-item {
  text-decoration: underline;

  @include breakpoint(medium down) {
    font-size: 80%;
  }
}

.item-info {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 100%;
}

.item-info__close {
  position: absolute;
  top: .5rem;
  right: .5rem;
}

.item-info__body {
  padding: 1rem;
  overflow-y: auto;

  img {
    margin-bottom: 1rem;

    @include breakpoint(medium down) {
      display: none;
    }
  }

  .legend-item {
    margin-bottom: 1rem;
  }
}

.intention-cta {
  display: inline-block;
}

.intention-cta + .intention-cta {
  margin-left: 1rem;
}
