


















































@import 'settings';

.v-autocomplete {
  position: relative;
  width: 100%;
}

.v-autocomplete-list {
  position: absolute;
  z-index: 9999;
  width: 100%;
  top: 40px;
  min-width: 400px;
  text-align: left;
  max-height: 300px;
  overflow-y: auto;
  border: 1px $dark-gray solid;
}

.v-autocomplete-list-item {
  color: $primary-color;
  cursor: pointer;
  background-color: $white;
  padding: .75em;
  border-bottom: 1px $dark-gray solid;

  &:last-child {
    border-bottom: 0;
  }

  &:hover,
  &.v-autocomplete-item-active {
    background-color: $light-gray;
  }
}
