





















































@import 'settings';

.scroll-top {
  position: fixed;
  right: 30px;
  bottom: 50px;
  z-index: 1000;

  background: $white;
  border: 1px $primary-color solid;
  cursor: pointer;
  opacity: 1;
  transition: 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;

  opacity: 1;
}

.scroll-top__area {
  font-size: 3em;
  padding: .5rem 1rem;

  @include breakpoint(medium) {
    font-size: 4em;
    // padding: 1rem;
  }
}

.back-top-fade-enter,
.back-top-fade-leave-active {
  transform: translateY(-30px);
  opacity: 0;
}
